@import url(https://fonts.googleapis.com/css?family=Raleway);
@import url(https://fonts.googleapis.com/css?family=Montserrat);
@import url(https://fonts.googleapis.com/css?family=Raleway);
@import url(https://fonts.googleapis.com/css?family=Montserrat);
/*
    General
*/
body {
  background: gray;
  color: #e0e0ee;
  font-family: 'Raleway', sans-serif; }

a {
  text-decoration: none;
  color: #e0e0ee;
  cursor: pointer; }

a:hover {
  text-decoration: underline; }

button, button::-moz-focus-inner {
  outline: none;
  border: 0; }

/* Hide HTML5 Up and Down arrows. */
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0; }

input[type="number"] {
  -moz-appearance: textfield; }

#root {
  display: flex;
  flex-direction: column;
  align-items: center; }

footer {
  width: 100%;
  margin-top: 30px;
  display: flex;
  justify-content: space-around;
  align-items: center; }

.TitleBar {
  color: #e0e0ee;
  width: 100%; }

.wrapper {
  margin: 0 auto;
  max-width: 400px;
  padding: 0 20px;
  text-align: center; }

/*
    App
*/
.App-button {
  background: transparent;
  color: #e0e0ee;
  cursor: pointer; }

.fa:hover, .App-button:hover {
  text-shadow: 0 0 5px rgba(204, 204, 238, 0.5); }

.fa-spin {
  margin: 10px; }

/*
    Hiscores
*/
.HiscorePage {
  box-shadow: 2px 5px 20px rgba(17, 17, 51, 0.9);
  padding: 20px;
  min-width: 400px; }

.HiscoreView, .HiscorePage {
  font-family: 'Montserrat', sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 10px;
  background: #111133; }

.Hiscores {
  border: 1px solid rgba(204, 204, 238, 0.5);
  box-shadow: 0 1px 0 rgba(17, 17, 51, 0.9);
  width: 100%;
  text-align: center; }
  .Hiscores .Hiscore {
    list-style: none;
    display: flex; }
    .Hiscores .Hiscore span {
      flex: 1 1;
      padding: 5px 0;
      text-align: center; }
    .Hiscores .Hiscore .HiscoreHeader {
      text-decoration: underline;
      font-weight: bold; }
    .Hiscores .Hiscore:nth-child(2) {
      color: gold !important;
      text-shadow: 0 0 3px rgba(17, 17, 51, 0.9); }
    .Hiscores .Hiscore:nth-child(even) {
      color: black;
      background: rgba(204, 204, 238, 0.5); }

/*
    Game
*/
.Game {
  box-shadow: 2px 5px 20px rgba(17, 17, 51, 0.9);
  color: #e0e0ee;
  background: #111133;
  text-align: center;
  padding: 20px; }
  .Game .Game-grid {
    margin-top: 10px;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none; }
    .Game .Game-grid .Game-grid-row {
      display: flex; }
  .Game .Game-info {
    font-family: 'Montserrat', sans-serif; }
  .Game .Game-menu {
    display: flex;
    flex-direction: column; }
    .Game .Game-menu h1 {
      margin: 10px 0;
      color: #e0e0ee; }
    .Game .Game-menu .Game-input {
      font-family: inherit;
      border: none;
      box-shadow: 0 0 2px 2px rgba(204, 204, 238, 0.5);
      text-align: center;
      font-size: 1rem;
      width: 2rem;
      font-weight: bold;
      background: rgba(204, 204, 238, 0.5);
      color: #111133;
      padding: 8px 20px;
      margin: 10px; }
    .Game .Game-menu .Game-input:focus {
      background: #e0e0ee;
      color: #111133; }
    .Game .Game-menu .Game-button {
      outline: none;
      font-family: inherit;
      position: relative;
      font-weight: bold;
      padding: 10px 20px;
      margin: 10px;
      color: #111133;
      background: rgba(204, 204, 238, 0.5);
      box-shadow: 0 1px 0 rgba(17, 17, 51, 0.9);
      border-radius: 10px; }
    .Game .Game-menu .Game-button:active, .Game .Game-menu .toggle {
      box-shadow: 0 0 0;
      top: 1px;
      background: #e0e0ee; }

/*
    Popup
*/
.Popup {
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  .Popup .Popup-inner {
    background: rgba(17, 17, 51, 0.9);
    color: #e0e0ee;
    border: 1px solid rgba(204, 204, 238, 0.5);
    box-shadow: 0 0 5px 5px rgba(17, 17, 51, 0.9);
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center; }
    .Popup .Popup-inner p {
      padding: 0.2em 0.4em;
      font-size: 3em;
      box-sizing: border-box;
      text-align: center;
      margin: 0; }
    .Popup .Popup-inner .Popup-score p {
      font-size: 1.5em; }
    .Popup .Popup-inner .Popup-score form {
      display: flex;
      align-items: flex-end; }
      .Popup .Popup-inner .Popup-score form .Popup-name {
        font-family: inherit;
        background: rgba(204, 204, 238, 0.5);
        color: #111133;
        margin: 10px;
        text-align: center;
        border: none;
        font-size: 1.3em;
        padding: 5px 0;
        width: 10em; }
      .Popup .Popup-inner .Popup-score form .Popup-name:focus {
        background: #e0e0ee; }
      .Popup .Popup-inner .Popup-score form .Popup-submit {
        border: none;
        font-family: inherit;
        font-weight: bold;
        padding: 10px 20px;
        margin: 10px;
        color: #111133;
        background: rgba(204, 204, 238, 0.5);
        box-shadow: 0 1px 0 rgba(17, 17, 51, 0.9);
        border-radius: 10px; }
      .Popup .Popup-inner .Popup-score form .Popup-submit:active {
        background: #e0e0ee; }
    .Popup .Popup-inner .close {
      font-family: inherit;
      margin-top: 20px;
      border: none;
      background: rgba(204, 204, 238, 0.5);
      color: #111133;
      cursor: pointer;
      padding: 5px 15px;
      font-size: 1.3em; }
    .Popup .Popup-inner .close:hover {
      background: #e0e0ee; }

/*
    Tile
*/
div[class*='Tile'] {
  text-align: center;
  width: 25px;
  height: 25px;
  box-sizing: border-box;
  box-shadow: 0 0 1px #111133;
  cursor: default;
  font-family: 'Montserrat', sans-serif;
  font-weight: 601;
  font-size: 1.3em;
  display: flex;
  justify-content: center;
  align-items: center;
  text-shadow: 0 0 1px rgba(204, 204, 238, 0.5); }

.Tile-dark {
  background: #21212b; }

.Tile-light {
  background: #e0e0ee; }

.Flag {
  color: orange; }

div[class*='Cover'] {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  box-shadow: 0 0 1px rgba(204, 204, 238, 0.5); }

.Cover-dark {
  background: #03030d; }

.Cover-light {
  background: rgba(17, 17, 51, 0.9); }

.Mine {
  color: gray;
  height: 100%; }

.nearby1 {
  color: #88f; }

.nearby2 {
  color: #494; }

.nearby3 {
  color: #e33; }

.nearby4 {
  color: #33f; }

.nearby5 {
  color: #b4e; }

.nearby6 {
  color: teal; }

.nearby7 {
  color: pink; }

.nearby8 {
  color: gold;
  text-shadow: 0 0 2px #e0e0ee; }

